class Board extends React.Component {
  rendername(price1, price2, price3, price4) {
    return (
      <ShoppingList
        price1={price1}
        price2={price2}
        price3={price3}
        price4={price4}
      />
    );
  }
  render() {
    return <div className="name">{this.rendername(250, 250, 250, 250)}</div>;
  }
}

class Splist extends React.Component {
  render() {
    return <Board />;
  }
}

